<script>
  import { toElement } from '../_utils/scroll.js';

  export let pageSelector = '';

  function scrollToNext() {
    const nextPage = document.querySelector(pageSelector);
    toElement(nextPage);
  }
</script>

<style>
  .bounce {
    animation: bounce 2s infinite;
  }

  @keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-30px);
    }
    60% {
      transform: translateY(-15px);
    }
  }
</style>

<div class='pointer' on:click={() => scrollToNext()}>
